'use client';

import { useState } from 'react';
import { 
  ChevronDown, 
  ChevronUp, 
  FileText, 
  HelpCircle, 
  DollarSign, 
  Calendar, 
  Gavel, 
  TrendingUp, 
  Shield, 
  Clock 
} from 'lucide-react';

const ChitFundFAQ = () => {
  const [activeItem, setActiveItem] = useState(null);

  const faqItems = [
    {
      id: 1,
      question: "What are the benefits of a chit fund?",
      answer: "Chit funds offer multiple benefits including regular savings, access to lump sum amounts without credit checks, attractive returns, and flexibility in bidding. They serve as both a savings instrument and a source of credit for emergencies or opportunities.",
      icon: <TrendingUp className="w-5 h-5" />
    },
    {
      id: 2,
      question: "How does a chit fund operate?",
      answer: "A chit fund operates with a group of subscribers who contribute a fixed amount monthly. Each month, an auction is conducted where subscribers bid for the prize amount. The lowest bidder wins and receives the prize amount after deducting the foreman's commission and the discount (bid amount).",
      icon: <HelpCircle className="w-5 h-5" />
    },
    {
      id: 3,
      question: "What are the modes of payment?",
      answer: "Payments can be made through various modes including cash, cheque, bank transfer, online payment gateways, UPI, and auto-debit instructions. Most chit fund companies offer multiple payment options for convenience.",
      icon: <DollarSign className="w-5 h-5" />
    },
    {
      id: 4,
      question: "When can I participate in the auction?",
      answer: "You can participate in the auction from the first month itself, unless you're the foreman who holds the first ticket. Each subscriber gets one chance to bid every month until they win the auction or the chit tenure completes.",
      icon: <Calendar className="w-5 h-5" />
    },
    {
      id: 5,
      question: "What is the purpose of bidding?",
      answer: "Bidding allows subscribers to access funds earlier than the chit tenure. The bid amount represents the discount you're willing to accept on the prize amount. Lower bids increase your immediate cash in hand, while higher bids help other subscribers earn more dividends.",
      icon: <Gavel className="w-5 h-5" />
    },
    {
      id: 6,
      question: "What are the Income Tax benefits from saving or borrowing?",
      answer: "Chit funds registered under the Chit Funds Act offer tax benefits. The amount saved is eligible for deduction under Section 80C up to â‚¹1.5 lakh. Interest earned may be tax-free up to certain limits, and the prize amount received is not treated as income.",
      icon: <FileText className="w-5 h-5" />
    },
    {
      id: 7,
      question: "What are the sureties to be submitted?",
      answer: "Typically, you need to submit identity proof, address proof, income proof, and 2-3 passport size photographs. Some companies may require guarantors or collateral for high-value chits. The foreman usually provides personal or property guarantee.",
      icon: <Shield className="w-5 h-5" />
    },
    {
      id: 8,
      question: "How soon can I expect the payment once I bid the chit?",
      answer: "Once you win the bid, the payment is usually processed within 24-48 hours. After deducting the foreman's commission and the discount amount, the net prize money is disbursed either by cheque or direct bank transfer. Some companies offer same-day disbursement for emergency cases.",
      icon: <Clock className="w-5 h-5" />
    }
  ];

  const toggleItem = (id) => {
    setActiveItem(activeItem === id ? null : id);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-50 p-4 md:p-8 flex items-center justify-center">
      <div className="max-w-4xl w-full bg-white rounded-2xl shadow-xl overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-900 to-indigo-800 text-white p-6 md:p-8">
          <div className="flex items-center gap-3 mb-2">
            <div className="p-2 bg-yellow-500 rounded-lg">
              <FileText className="w-8 h-8" />
            </div>
            <h1 className="text-2xl md:text-3xl font-bold">All About Chit Fund</h1>
          </div>
          
          <div className="mt-4 p-4 bg-blue-800/30 rounded-lg border border-blue-700/50">
            <div className="flex items-start gap-3">
              <div className="mt-1">
                <div className="w-3 h-3 bg-yellow-400 rounded-full"></div>
              </div>
              <div>
                <p className="font-semibold text-lg">What is a ticket?</p>
                <p className="text-blue-100 mt-1">
                  A &quot;ticket&quot; means the share of a subscriber in a chit. The foreman always has the first ticket.
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* FAQ Items */}
        <div className="p-4 md:p-8">
          <h2 className="text-xl md:text-2xl font-bold text-gray-800 mb-6 flex items-center gap-2">
            <HelpCircle className="w-6 h-6 text-indigo-600" />
            Frequently Asked Questions
          </h2>
          
          <div className="space-y-4">
            {faqItems.map((item) => (
              <div 
                key={item.id} 
                className="border border-gray-200 rounded-xl overflow-hidden shadow-sm hover:shadow-md transition-shadow duration-300"
              >
                <button
                  className="w-full p-5 text-left flex items-center justify-between bg-gradient-to-r from-gray-50 to-white hover:from-indigo-50 hover:to-blue-50 transition-all duration-300"
                  onClick={() => toggleItem(item.id)}
                  aria-expanded={activeItem === item.id}
                >
                  <div className="flex items-center gap-4">
                    <div className="p-2 bg-indigo-100 rounded-lg text-indigo-600">
                      {item.icon}
                    </div>
                    <span className="font-semibold text-gray-800 text-lg">
                      {item.question}
                    </span>
                  </div>
                  <div className="ml-4">
                    {activeItem === item.id ? (
                      <ChevronUp className="w-5 h-5 text-indigo-600" />
                    ) : (
                      <ChevronDown className="w-5 h-5 text-gray-500" />
                    )}
                  </div>
                </button>
                
                <div
                  className={`overflow-hidden transition-all duration-500 ease-in-out ${
                    activeItem === item.id 
                      ? 'max-h-96 opacity-100' 
                      : 'max-h-0 opacity-0'
                  }`}
                >
                  <div className="p-5 pt-0">
                    <div className="pl-12">
                      <div className="p-4 bg-blue-50 rounded-lg border-l-4 border-indigo-500">
                        <p className="text-gray-700 leading-relaxed">
                          {item.answer}
                        </p>
                      </div>
                      
                      {item.id === 1 && (
                        <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-3">
                          <div className="flex items-center gap-2 text-sm text-green-600">
                            <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                            <span>Regular Savings Habit</span>
                          </div>
                          <div className="flex items-center gap-2 text-sm text-green-600">
                            <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                            <span>No Credit Checks</span>
                          </div>
                          <div className="flex items-center gap-2 text-sm text-green-600">
                            <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                            <span>Higher Returns than Banks</span>
                          </div>
                          <div className="flex items-center gap-2 text-sm text-green-600">
                            <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                            <span>Emergency Fund Access</span>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Summary Stats */}
          <div className="mt-10 p-6 bg-gradient-to-r from-indigo-50 to-blue-50 rounded-xl border border-indigo-100">
            <h3 className="text-lg font-semibold text-gray-800 mb-4">Chit Fund Overview</h3>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="text-center p-4 bg-white rounded-lg shadow-sm">
                <div className="text-2xl font-bold text-indigo-600">1st</div>
                <div className="text-sm text-gray-600 mt-1">Foreman&apos;s Ticket</div>
              </div>
              <div className="text-center p-4 bg-white rounded-lg shadow-sm">
                <div className="text-2xl font-bold text-indigo-600">Monthly</div>
                <div className="text-sm text-gray-600 mt-1">Auction Cycle</div>
              </div>
              <div className="text-center p-4 bg-white rounded-lg shadow-sm">
                <div className="text-2xl font-bold text-indigo-600">24-48h</div>
                <div className="text-sm text-gray-600 mt-1">Payment Processing</div>
              </div>
              <div className="text-center p-4 bg-white rounded-lg shadow-sm">
                <div className="text-2xl font-bold text-indigo-600">80C</div>
                <div className="text-sm text-gray-600 mt-1">Tax Benefits</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChitFundFAQ;